import React, { Component } from 'react';

import Row from './Row';
import Cell from './Cell';

import DeviceReminder from './device/Reminder';

class Reminder extends Component {
  render() {
    return (
      <section>
        <Row>
          <Cell>
            <h2>Pysy ajan tasalla hoitorutiineista</h2>
            <p>Pidä kirjaa koirasi säännöllisistä hoitotoimenpiteistä kuten rokotuksista ja madotuksista. Dogtivity muistuttaa ajankohtien lähestyessä ja saat kätevästi varattua koirallesi myös lääkäriajan.</p>
            <p>Saat muistutuksia myös muista koirasi hoitorutiineista kuten kynsienleikkuusta ja turkinhoidosta. Voit muokata tehtävät ja muistutukset vastaamaan sinun ja koirasi yksilöllisiä aikatauluja ja tarpeita.</p>
          </Cell>
          <Cell>
            <DeviceReminder />
          </Cell>
        </Row>
      </section>
    );
  }
}

export default Reminder;



