import React, { Component } from 'react';

import Row from './Row';
import Cell from './Cell';

import DeviceTasks from './device/Tasks';

class Tasks extends Component {
  render() {
    return (
      <section>
        <Row>
          <Cell>
            <h2>Kilpaile perheen kesken suorittamalla tehtäviä</h2>
            <p>Kerää henkilökohtaisia pisteitä koiran hoitamisesta: mitä vaativampi tai aikaavievämpi tehtävä on, sitä enemmän pisteitä saat!</p>
            <p>Pisteet toimivat rahayksikköinä Dogtivityn sisäisessä hauskassa peliosiossa, jossa etenkin perheen nuorimmaiset voivat luoda oman virtuaalikoiran ja ostella sille erilaisia tarvikkeita. Pisteiden avulla perheenjäsenet myös näkevät kuinka tasapuolisesti hoitotoimenpiteet oikeassa elämässä jakautuvat.</p>
          </Cell>
          <Cell>
            <DeviceTasks />
          </Cell>
        </Row>
      </section>
    );
  }
}

export default Tasks;



