import React, { Component } from 'react';

import Row from './Row';
import Cell from './Cell';

import DeviceExercise from './device/Exercise';

class Exercise extends Component {
  constructor(props) {
    super(props);
    this.state = {key: 0};
  }

  updateKey() {
    let key = this.state.key + 1;
    this.setState({key: key});
  }

  render() {
    return (
      <section>
        <Row>
          <Cell>
            <DeviceExercise />
          </Cell>
          <Cell>
            <h2>Seuraa koirasi aktiivisuutta</h2>
            <p>Dogtivitylla voit tallentaa ja tarkastella yhteisiä ulkoilulenkkejänne kartalla. Liittämällä Dogtivityyn oheislaitteita, kuten koirille suunnitellun älyliivin, voit seurata koirasi päivittäistä aktiivisuutta ja energiankulutusta entistä tarkemmin.</p>
          </Cell>
        </Row>
      </section>
    );
  }
}

export default Exercise;



